@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.jumbotron{display:flex;justify-content:center;flex-direction:column;background-color:#0e0e0e;background:radial-gradient(ellipse at 60% 48%, rgba(255,255,255,0.07) 0%, rgba(14,14,14,0) 60%);height:calc(100vh - 72px)}@-moz-document url-prefix(){.jumbotron{background:unset}}@media (min-aspect-ratio: 3 / 2){.jumbotron{flex-direction:row}}.jumbotron .MuiTypography-root{display:flex;flex-direction:column;justify-content:center;align-items:center;color:#fff;flex-basis:100%;text-align:center}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root{flex-grow:0;flex-shrink:0;flex-basis:40%}}.jumbotron .MuiTypography-root .name{font-size:4rem;font-weight:200;line-height:1;padding:0 20px}@media (min-aspect-ratio: 3 / 2) and (max-width: 1280px){.jumbotron .MuiTypography-root .name{font-size:3rem}}.jumbotron .MuiTypography-root .title{font-size:1.5rem;line-height:1;font-weight:500}@media (min-aspect-ratio: 3 / 2){.jumbotron .MuiTypography-root .title{font-size:1rem}}.jumbotron .img{display:flex;flex-direction:column;justify-content:flex-end;align-items:flex-start}.jumbotron .img img{max-height:100%}@media (min-aspect-ratio: 9 / 10) and (max-aspect-ratio: 3 / 2){.jumbotron .img img{max-width:80%;margin:0 10%}}

*{box-sizing:border-box;padding:0;margin:0}body{background-color:#0e0e0e;width:100vw}div,header,section,article,img{width:100%}div{height:100%}

